  background-color: #E6A23C !important;
  margin: 10px !important;
  width: auto !important;
}

.divider>>>.el-divider {
  background-color: #606266 !important;
  margin: 5px !important;
  width: auto !important;
}

.eldivider>>>.el-divider {
  margin: 1px;
  width: auto;
}
</style>



